import React, { useState } from 'react'
import { Nav, Navbar, NavDropdown } from 'react-bootstrap'
import "./uasses.css"
import Modal from 'react-modal'

function Menu() {
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [poupisopen , setpoupisopen]= useState(false);

const  handlesubmit = ()=>{
    alert("Login Successfully..")

}

const  handlesign = ()=>{
    alert("Signup Successfully..")

}



    return (
        <div>
           {/* menu start */}

<Navbar className='menu'  variant="dark"
        sticky="top" expand="md" collapseOnSelect>
        <Navbar.Brand className='menu-brand'>
        <span className='home'> om </span>  TestYourself
        </Navbar.Brand>

        <Navbar.Toggle className='red' />
        <Navbar.Collapse   >
          <Nav className='ms-auto'>
            
            
 <Nav.Link className="coloring"  onClick={() => setModalIsOpen(true)} >Login</Nav.Link>
<Nav.Link className="coloring"  onClick={() => setpoupisopen(true)} >Sign up <span className='home'> om </span></Nav.Link>


<Modal 

isOpen={modalIsOpen} 
onRequestClose={() => setModalIsOpen(false)} >
<h1 className='login-heading'>Login</h1>

<form onSubmit={ handlesubmit }>
<label className='form-label'>User Name</label> <br />
<input type="text" required className='form-input' ></input> <br />

<label className='form-label'>Password</label> <br />
<input type="password" required className='form-input' ></input> <br />

<button type='submit' class="btn btn-info">Submit</button>

</form>

 <div>
 <button className='close-modal' onClick={() => setModalIsOpen(false)}>X</button>
</div>
</Modal>


<Modal 
isOpen={poupisopen} 
onRequestClose={() => setpoupisopen(false)} >
<h1 className='login-heading'>Sign up</h1>

<form onSubmit={ handlesign }>
<label className='form-label'>User Name</label> <br />
<input type="text" required className='form-input' ></input> <br />

<label className='form-label'>Email</label> <br />
<input type="email" required className='form-input' ></input> <br />

<label className='form-label'>Password</label> <br />
<input type="password" required className='form-input' ></input> <br />

<button type='submit' class="btn btn-info">Submit</button>

</form>







 <div>
 <button className='close-modal' onClick={() => setpoupisopen(false)}>X</button>
</div>
</Modal>






</Nav>
</Navbar.Collapse>

      </Navbar>

{/* menu end */}
        </div>
    )
}

export default Menu
