import React from 'react'
import "./uasses.css"
import "bootstrap/dist/css/bootstrap.min.css";
import Modal from "react-bootstrap/Modal";
import ModalBody from "react-bootstrap/ModalBody";
import ModalHeader from "react-bootstrap/ModalHeader";
import ModalFooter from "react-bootstrap/ModalFooter";
import ModalTitle from "react-bootstrap/ModalTitle";

function Footer() {

    const  handlefooter = ()=>{
        alert("Our Team will Contact you soon..")
    
    }




    return (
        <div>
            <div className="footer-section">
               <div className="footer-flex">

                   <div className="social">
                       <h5 className='footer-center'>We Love to Chat</h5>
                     
                   </div>

                   <div className="footer-btn">
                       {/* modal button */}
                   <button type="button" class="btn btn-warning " data-bs-toggle="modal" data-bs-target="#exampleModal" >Contact us</button>

                   {/* modal */}
                   <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Contact us</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
<form onSubmit={ handlefooter }>
<label className='form-label'>User Name</label> <br />
<input type="text" required className='form-input-footer' ></input> <br />

<label className='form-label'>Email</label> <br />
<input type="email" required className='form-input-footer' ></input> <br />

<label className='form-label'>Phone Number</label> <br />
<input type="number" required className='form-input-footer' ></input> <br />

<button type='submit' class="btn btn-info">Submit</button>

</form>








        
      </div>
      
    </div>
  </div>
</div>


                   




{/* modal end */}
                   </div>
                    </div>
            </div>


        </div>
    )
}

export default Footer
